<template>
  <div class="content">
    <el-form
      :inline="true"
      :model="queryForm"
      size="small"
      class="demo-form-inline"
    >
      <el-form-item label="">
        <el-input
          clearable
          @input="inputChange"
          v-model="queryForm.name"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="startQuery"
          ></el-button
        >
        <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="startCreate"
          ></el-button
        >
        <el-button
          type="primary"
          icon="el-icon-upload"
          @click="showUploadDialog = !showUploadDialog"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <el-table :data="templateData" size="mini" style="width: 100%">
      <el-table-column prop="templateId" label="ID"> </el-table-column>
      <el-table-column prop="name" label=""> </el-table-column>
      <el-table-column prop="description" label=""> </el-table-column>
      <!-- <el-table-column prop="author" label=""> </el-table-column> -->
      <el-table-column prop="updateTime" label="">
        <template slot-scope="scope">
          {{ scope.row.updateTime | dateFormat }}
        </template>
      </el-table-column>
      <el-table-column align="right">
        <template slot-scope="scope" v-if="scope.row.templateType == 1">
          <el-button
            type="primary"
            size="mini"
            plain
            @click="refreshTemplate(scope.row)"
            ></el-button
          >
          <el-button size="mini" @click="handleEdit(scope.row)" plain
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            plain
            @click="handleDelete(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div>
      <el-pagination
        :page-size="10"
        :current-page="currentPage"
        @current-change="pageChange"
        layout="prev, pager, next"
        :total="totalSize"
      >
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog :visible.sync="showDialog" @close="closeDialog" width="85%">
      <template slot="title">
        {{ dialogTitle }}
        <el-tooltip effect="dark" placement="right-start">
          <div slot="content">
            <br />
          </div>
          <span
            ><i
              style="margin-left: 5px; cursor: pointer; font-size: 15px"
              class="el-icon-question"
          /></span>
        </el-tooltip>
      </template>
      <TemplateConfig
        :isEdit="isEdit"
        :config="infoForm"
        @dataChange="templateChange"
        @complete="closeDialog"
      />
    </el-dialog>
    <!--  -->
    <!--  -->
    <el-dialog
      :visible.sync="showUploadDialog"
      title=""
      :destroy-on-close="true"
      width="60%"
    >
      <div v-if="parseData.length <= 0">
        <el-upload
          drag
          action="#"
          :auto-upload="true"
          :http-request="httpRequest"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">jar</div>
        </el-upload>
      </div>
      <div v-else>
        <h3></h3>
        <el-collapse>
          <el-collapse-item
            name="1"
            v-for="(item, index) in parseData"
            :key="index"
          >
            <template slot="title">
              {{ item.name }} - {{ item.description }}
            </template>
            <TemplateConfig
              :config="item"
              @dataChange="uploadChange($event, index)"
            />
          </el-collapse-item>
        </el-collapse>
      </div>

      <span slot="footer" class="dialog-footer" v-if="parseData.length > 0">
        <el-button size="mini" @click="deletePlugin"></el-button>
        <el-button type="primary" size="mini" @click="submitTemplates"
          ></el-button
        >
      </span>
    </el-dialog>
    <!--  -->
  </div>
</template>
<script>
import templateApi from '../../http/Template'
import TemplateConfig from './comp/template-config.vue'
export default {
  components: {
    TemplateConfig,
  },
  data() {
    return {
      templateData: [],
      queryForm: {},
      showDialog: false,
      infoForm: { params: [{}] },
      dialogTitle: '',
      isEdit: false,
      currentPage: 1,
      totalSize: 0,
      showUploadDialog: false,
      parseData: [],
      pluginId: '',
    }
  },
  methods: {
    deletePlugin() {
      templateApi.deletePlugin(this.pluginId).then((res) => {
        if (res.data) {
          this.$message.success('')
        } else {
          this.$message.warning('')
        }
      })
    },
    templateChange(data) {
      this.infoForm = data
    },
    submitTemplates() {
      let data = {
        templates: this.parseData,
        pluginId: this.pluginId,
      }
      templateApi.batchCreateTemplate(data).then((res) => {
        if (res.data) {
          this.$message.success('')
          this.showUploadDialog = false
          this.parseData = []
          this.pluginId = ''
          this.getTemplatePage(1)
        } else {
          this.$message.error('')
        }
      })
    },
    uploadChange(data, index) {
      this.parseData[index] = data
    },
    httpRequest(param) {
      const formData = new FormData()
      formData.append(`file`, param.file)
      templateApi.upload(formData).then((res) => {
        if (res.data) {
          this.$message.success('')
          this.parseData = res.data.templateDefines
          this.pluginId = res.data.pluginId
        } else {
          this.$message.error('')
        }
      })
    },
    removeHeader(index) {
      this.infoForm.headers.splice(index, 1)
    },
    addHeader() {
      this.infoForm.headers.push({})
    },
    inputChange() {
      this.getTemplatePage(1)
    },
    pageChange(page) {
      this.getTemplatePage(page)
    },
    getTemplatePage(page) {
      let name = this.queryForm.name
      if (!name) {
        name = ''
      }

      templateApi.getTemplatePage(page, 10, name).then((res) => {
        this.templateData = res.data.data
        this.totalSize = res.data.total
      })
    },
    startQuery() {
      this.getTemplatePage(1)
    },
    startCreate() {
      this.isEdit = false
      this.showDialog = !this.showDialog
      this.dialogTitle = ''
      this.infoForm = { params: [{}], invokeType: 1, method: '' }
    },
    refreshTemplate(row) {
      templateApi.refresh(row.templateId).then((res) => {
        if (res.data) {
          this.$message.success('')
        } else {
          this.$message.error('')
        }
      })
    },
    handleEdit(row) {
      this.isEdit = true
      this.showDialog = true
      this.dialogTitle = ''
      this.infoForm = row

      // let rowData = JSON.parse(JSON.stringify(row))
      // if (rowData.headers && rowData.headers.length > 0) {
      //   let array = []
      //   Object.keys(rowData.headers).forEach((key) => {
      //     array.push({ key: key, value: rowData.headers[key] })
      //   })
      //   rowData.headers = array
      // } else {
      //   rowData.headers = [{ key: '', value: '' }]
      // }

      // if (!rowData.params) {
      //   this.infoForm = rowData
      //   return
      // }

      // rowData.params.forEach((e) => {
      //   if (e.defaultValue === null) {
      //     return
      //   }
      //   if (e.type == 2) {
      //     e.range = e.defaultValue.range
      //   }

      //   e.defaultValue = e.defaultValue.defaultValue
      // })
      // this.infoForm = rowData
    },
    handleDelete(row) {
      this.$confirm(`${row.name}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        templateApi.deleteTemplate(row.templateId).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.getTemplatePage(1)
          } else {
            this.$message.error('')
          }
        })
      })
    },
    closeDialog() {
      this.showDialog = false
      this.infoForm = { params: [{}] }
      this.getTemplatePage(1)
    },
  },
  created() {
    this.getTemplatePage(1)
  },
}
</script>
<style scoped>
.content {
  margin: 5px;
}
</style>
