<script>
import serviceApi from '../../http/Service'
export default {
  data() {
    return {
      activeName: 'preview',
      filterText: '',
      apiForm: {},
      paramData: [],
      responseData: [],
      apiTreeData: [],
      defaultProps: {
        children: 'children',
        label: 'paramKey',
      },
      apiProps: {
        children: 'children',
        label: 'apiName',
      },
      pathData: [],
      headerData: [],
      bodyData: [],
      previewRes: [],
      uuid: 1,
      serviceList: [],
      serviceId: '',
      currentApi: '',
      showCreateApi: false,
      dataForm: { type: 'http' },
      apiRule: {
        apiName: [{ required: true, message: '', trigger: 'blur' }],
        type: [{ required: true, message: '', trigger: 'blur' }],
        api: [
          { required: true, message: 'api', trigger: 'blur' },
          {
            validator: this.validApi,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      createDir: false,
      selectNodes: [],
      isLeaving: false,
      updateApi: false,
    }
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val)
    },
    apiForm: {
      handler(val, old) {
        if (old && old.apiName) {
          this.updateApi = true
          console.log('xxxxx apiForm', val, old)
        }
      },
      deep: true,
      immediate: true,
    },
    paramData: {
      handler(val, old) {
        if (old && old.length > 0) {
          this.updateApi = true
          console.log('xxxxx paramData', val, old)
        }
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    allowApiDrop(draggingNode, targetNode, type) {
      let api = targetNode.data
      if (type == 'inner' && api.isApi) {
        return false
      }
      return true
    },
    handleApiDragEnd(draggingNode, targetNode) {
      let api = JSON.parse(JSON.stringify(draggingNode.data))
      api.parentId = targetNode.data.apiId
      if (targetNode.data.isApi) {
        api.parentId = null
      }
      api.requestParams = JSON.parse(api.requestParams)
      api.responseParams = JSON.parse(api.responseParams)
      serviceApi.updateApi(api)
    },
    beforeLeave(activeName, oldActiveName) {
      if (oldActiveName == 'edit' && !this.isLeaving && this.updateApi) {
        this.$confirm(
          '?',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning',
          }
        )
          .then(() => {
            this.isLeaving = true
            this.activeName = activeName
            this.selectTab()
            this.updateApi = false
          })
          .catch(() => {})
        return false
      }
      return true
    },
    selectTreeNode(data, checked) {
      if (checked) {
        this.selectNodes.push(data.apiId)
      } else {
        let index = this.selectNodes.indexOf(data.apiId)
        if (index != -1) {
          this.selectNodes.splice(index, 1)
        }
      }
    },
    validApi(rule, value, callback) {
      if (this.dataForm.method == '' || this.dataForm.method == null) {
        callback(new Error())
      } else {
        callback()
      }
    },
    selectItemCommand(command, data) {
      console.log('sssss', command, data)
      if (command == 'dir') {
        this.addFolder(data)
        return
      }
      if (command == 'api') {
        this.addApi(data)
        return
      }
      if (command == 'delete') {
        serviceApi.deleteApi(data.apiId).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.selectService()
          } else {
            this.$message.error('')
          }
        })
      }
    },
    treeNodeSelect(data) {
      if (!data.isApi) {
        return
      }
      this.apiForm = JSON.parse(JSON.stringify(data))
      this.paramData = JSON.parse(data.requestParams)
      if (!this.paramData) {
        this.paramData = []
      }

      this.responseData = JSON.parse(data.responseParams)
      if (!this.responseData) {
        this.responseData = []
      }
      this.currentApi = data.apiId
      this.updateApi = false
      this.selectTab()
    },
    addFolder(node) {
      this.createDir = true
      this.showCreateApi = true
      this.dataForm.parentId = node.apiId
      this.dataForm.isApi = false
    },
    addApi(node) {
      this.createDir = false
      this.showCreateApi = true
      this.dataForm.parentId = node.apiId
      this.dataForm.isApi = true
    },
    submitApi(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }

        this.dataForm.serviceId = this.serviceId
        serviceApi.createApi(this.dataForm).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.selectService()
            this.cancelCreate()
          } else {
            this.$message.error('')
          }
        })
      })
    },
    selectCommand(command) {
      if (command == 'delete') {
        serviceApi.batchDeleteApi(this.selectNodes).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.selectService()
          } else {
            this.$message.error('')
          }
        })
        return
      }
      this.createDir = command == 'dir'
      this.showCreateApi = true
      this.dataForm.isApi = !this.createDir
    },
    cancelCreate() {
      this.showCreateApi = false
      this.dataForm = { type: 'http' }
    },
    saveApi() {
      let data = this.apiForm
      data.serviceId = this.serviceId
      data.requestParams = this.paramData
      data.responseParams = this.responseData
      if (this.currentApi != '') {
        serviceApi.updateApi(data).then((res) => {
          if (res.data) {
            this.$message.success('')
            this.updateApi = false
          } else {
            this.$message.error('')
          }
        })
        return
      }
      serviceApi.createApi(data).then((res) => {
        if (res.data) {
          this.$message.success('')
          this.updateApi = false
        } else {
          this.$message.error('')
        }
      })
    },
    selectTab() {
      this.isLeaving = false
      if (this.activeName == 'preview') {
        this.pathData = []
        this.headerData = []
        this.bodyData = []
        this.traverseTree(this.paramData, 1)
        this.paramData.forEach((e) => {
          if (e.position == 'Path' || e.position == 'Query') {
            this.pathData.push(e)
          }
          if (e.position == 'Header') {
            this.headerData.push(e)
          }
          if (e.position == 'Body') {
            this.bodyData.push(e)
          }
        })
        this.previewRes = JSON.parse(JSON.stringify(this.responseData))
        this.traverseTree(this.previewRes, 1)
      }
    },
    traverseTree(nodes, id) {
      for (const node of nodes) {
        node.id = id
        id++
        if (node.children && node.children.length > 0) {
          this.traverseTree(node.children, id)
        }
      }
    },
    filterNode(value, data) {
      if (!value) return true
      return data.apiName.indexOf(value) !== -1
    },
    removeParam(node, data) {
      const parent = node.parent
      const children = parent.data.children || parent.data
      const index = children.findIndex((d) => d.id === data.id)
      children.splice(index, 1)
    },
    addParam() {
      this.paramData.push({ id: this.uuid, paramKey: '', children: [] })
      this.uuid++
    },
    addSubParam(data) {
      if (data.children) {
        data.children.push({
          id: this.uuid,
          paramKey: '',
          children: [],
          position: data.position,
          freezed: true,
        })
        this.uuid++
        return
      }
      data.children = [
        {
          id: this.uuid,
          paramKey: '',
          children: [],
          position: data.position,
          freezed: true,
        },
      ]
      this.uuid++
    },
    addResParam() {
      this.responseData.push({ id: this.uuid, paramKey: '', children: [] })
      this.uuid++
    },
    selectService() {
      serviceApi.getApiList(this.serviceId).then((res) => {
        this.apiTreeData = this.buildTree(res.data)
      })
    },
    buildTree(data, parentId = null) {
      let tree = []
      for (let item of data) {
        if (item.parentId === parentId) {
          let children = this.buildTree(data, item.apiId)
          if (children.length > 0) {
            item.children = children
          }
          if (!item.children) {
            item.children = []
          }
          tree.push(item)
        }
      }
      return tree
    },

    getServices() {
      this.serviceList = []
      serviceApi.getServices().then((res) => {
        this.serviceList = res.data
        this.serviceId = this.serviceList[0].serviceId
        this.selectService()
      })
    },
  },
  created() {
    this.getServices()
  },
}
</script>
