import Vue from "vue";

Vue.filter("dateFormat", function (timestamp) {
  if (!timestamp) {
    return "";
  }
  var today = new Date(timestamp);

  //
  var DD = String(today.getDate()).padStart(2, "0"); // 
  var MM = String(today.getMonth() + 1).padStart(2, "0"); //1  0
  var yyyy = today.getFullYear(); // 

  // 
  let hh = String(today.getHours()).padStart(2, "0"); //(0-23)
  let mm = String(today.getMinutes()).padStart(2, "0"); //(0-59)
  let ss = String(today.getSeconds()).padStart(2, "0"); //(0-59)
  return yyyy + "-" + MM + "-" + DD + " " + hh + ":" + mm + ":" + ss;
});

Vue.filter("statusFormat", function (status) {
  if (!status) {
    return "";
  }
  switch (status) {
    case 1:
      return "success";
    case 2:
      return "danger";
    case 3:
      return "info";
    case 4:
      return "primary";
    case 5:
      return "wrning";
    case 6:
      return "info";
  }
  return "info";
});

